@import url("./Nullify.scss");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
body {
  min-height: 100%;

  display: flex;
  flex-direction: column;
  font-family: Poppins;
}

.App {
  height: 100%;
  background-color: #fafbff;
  max-width: 1400px;
  margin: 0px auto;

  padding: 0px 15px;
  box-sizing: content-box;
}
main {
  width: 100%;
  height: 100%;
  @media (max-width: 1399.98px) {
    padding: 8rem 3rem 16.125rem 1.5rem;
  }
  @media (max-width: 1199.98px) {
    padding: 8rem 1.5rem 16.125rem 0.5rem;
  }

  padding: 8rem 5rem 16.125rem 3.5rem;
}
.app-content {
  display: flex;
}
// SideMenu
.side-menu {
  padding: 36px 28px 76px 28px;
  background-color: #ffffff;
  width: 306px;

  display: flex;
  flex-direction: column;
  position: relative;

  z-index: 2;

  display: flex;
  flex-direction: column;
  @media (max-width: 991.98px) {
    position: absolute;
    left: 0;
    transition: all 0.3s;
    &.menu-hide {
      left: -290px;

      & .side-menu__content {
        & svg {
          transform: rotate(180deg);
          right: -100px;
        }
      }
    }
  }
}
.logo-for-closed-menu {
  position: absolute;
  top: 37px;
  left: 25px;
  z-index: 1;
}

.side-menu__content {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
  position: relative;
  & svg {
    position: absolute;
    top: 5px;
    right: -20px;
    width: 20px;
    padding: 5px;
    height: 20px;
    cursor: pointer;
    transition: all 0.3s;
  }
}
.logo {
  margin-bottom: 67px;
  & img {
    margin-right: 11px;
  }
  display: flex;

  align-items: center;
  font-weight: 600;
  font-size: 26px;
  line-height: 150%;
  letter-spacing: 0.01em;
  color: #000000;
  & span {
    margin-left: 4px;
    font-weight: 500;
    font-size: 10px;
    line-height: 150%;
    letter-spacing: -0.01em;
    color: #838383;
  }
}
.main-user {
  position: absolute;
  bottom: 5%;

  margin-top: 2.5rem;
  display: flex;
  align-items: center;
  gap: 12px;
}

.name {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;

  letter-spacing: 0.01em;

  color: #000000;
}
.prof {
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  /* identical to box height */

  letter-spacing: 0.01em;

  color: #757575;
}
.nav-menu {
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;
  gap: 2.5rem;
}
.name-menu {
  display: flex;
  align-items: center;
  gap: 15.25px;
  border-radius: 8px;
  padding: 11px 11px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;

  letter-spacing: -0.01em;

  color: #9197b3;
}
.ul-menu {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
.li-menu {
  display: flex;
  align-items: center;
  gap: 15.25px;
  border-radius: 8px;
  padding: 11px 11px;
  transition: all 0.3s;
  &:hover {
    background: #5932ea;
  }
  position: relative;
  cursor: pointer;
  &::after {
    content: "";
    background: url("./components/Icons/arrow-right.svg");
    position: absolute;
    right: 5%;
    bottom: 33%;
    width: 16px;
    height: 16px;
  }
  &.menu-active {
    background: #5932ea;
    & a {
      color: #fff;
    }
    &::after {
      content: "";
      background: url("./components/Icons/arrowWhite.svg");
      position: absolute;
      right: 5%;
      bottom: 33%;
      width: 16px;
      height: 16px;
    }
  }
  & a {
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;

    letter-spacing: -0.01em;

    color: #9197b3;
  }
}

.hello {
  margin-bottom: 51px;
  display: flex;
  align-items: center;
  font-size: 24px;
  & img {
    margin-left: 4px;
    max-width: 26px;
    max-height: 26px;
  }
  & h2 {
    font-weight: 600;

    line-height: 36px;
    /* identical to box height */

    color: #000000;
  }
}
.main-block_header {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  & .main-block_titles {
    gap: 7px;
    & h2 {
      font-weight: 600;
      font-size: 22px;
      line-height: 33px;
      /* identical to box height */

      letter-spacing: -0.01em;

      color: #000000;
    }
    & h3 {
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      /* identical to box height */

      letter-spacing: -0.01em;

      color: #16c098;
    }
  }
  & .search {
    & form {
      gap: 13px;
      display: flex;
      align-items: center;
      padding: 10px 11px;
      & svg {
        color: #7e7e7e;
      }
      & input {
        position: relative;
        display: inline-block;
      }
    }
  }
}
.table {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 2.375rem;
  @media (max-width: 767.98px) {
    overflow: auto;

    display: block;
  }
  & thead {
    width: 100%;
    height: 100%;

    & tr {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      @media (max-width: 767.98px) {
        grid-template-columns: repeat(6, 150px);
      }

      width: 100%;
      & th {
        cursor: pointer;
        border-radius: 2px;
        &:not(:last-child) {
          color: #00000044;
          padding-right: 0.5rem;
          padding-left: -0.5rem;
        }
        text-align: start;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        /* identical to box height */

        letter-spacing: -0.01em;

        color: #b5b7c0;
      }
    }
  }
  & tbody {
    display: flex;
    flex-direction: column;
    gap: 40px;
    & tr {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      @media (max-width: 767.98px) {
        grid-template-columns: repeat(6, 150px);
      }

      width: 100%;
      align-items: center;
      justify-content: space-between;
      & th {
        &:not(:last-child) {
          padding-right: 0.5rem;
          padding-left: -0.5rem;
        }
        text-align: start;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        /* identical to box height */

        letter-spacing: -0.01em;

        color: #292d32;

        &.Active {
          width: 80px;
          height: 29px;

          background: rgba(22, 192, 152, 0.38);
          border: 1px solid #00b087;
          border-radius: 4px;
          text-align: center;
          font-weight: 500;
          font-size: 14px;
          line-height: 21px;
          /* identical to box height */

          letter-spacing: -0.01em;

          color: #008767;
        }
        &.Inactive {
          width: 80px;
          height: 29px;
          text-align: center;

          background: #ffc5c5;
          border: 1px solid #df0404;
          border-radius: 4px;
          font-weight: 500;
          font-size: 14px;
          line-height: 21px;
          /* identical to box height */

          letter-spacing: -0.01em;

          color: #df0404;
        }
      }
    }
  }
}
